


















import mTab from "@/components/m-tab.vue";
import mTokenCard from "@/components/m-token-card.vue";
export default {
	data() {
		return {
			tabActive:"1",
			tabList:[
				{
					label:"未使用",
					id:"0",
				},
				{
					label:"已使用",
					id:"1",
				},
				{
					label:"已失效",
					id:"2",
				}
			]
		};
	},
	components:{
		mTab,
		mTokenCard
	},
	methods:{
		// tab栏点击
		tabChange(item){
			this.tabActive= item.id;
			this.getTokencards(item.id);
		},
		// 获取订单
		getTokencards(type){
			this.mPost('/server/co/myCoupons',{
				type:type,
				start:1,
				length:1000
			}).then(res=>{
				
				console.log(res);
			}).catch(err=>{
				console.log(err);
			});
		},
	},
	onLoad(){
		this.getTokencards(0)
	}
}
